// WIDESCREEN

@include mediaXl{
.projects{
    grid-template-columns: repeat(4, 1fr);
}
}


@include mediaLg{
    .projects{
        grid-template-columns: repeat(3, 1fr);
    }
}


@include mediaMd{
    main {
        align-items: center;
        text-align: center;
        .lg-heading{
            line-height: 1;
            margin-bottom: 1rem;
        }
    }

    ul.menu-nav,
    div.menu-branding {
        float: none;
        width: 100%;
        min-height: 0;

       &.show{
           transform: translate3d(0,0,0);
           font-size: 24px;
       } 
    }

    .menu-nav{
        height: 75vh;
        transform: translate3d(-100%,0,0);
    }
    .menu-branding{
        height: 25vh;
        transform: translate3d(100%,0,0);
    }
    
    .portrait
    {
        // background: url('/dist/img/descargaSm.png');
        width: 160px;
        height: 160px;
    }
    .about-info{
        grid-template-areas:
        'bioimage'
        'bio'
        'job1'
        'job2'
        'job3';
        grid-template-columns: 1fr;
    }
    .projects{
        grid-template-columns: repeat(2, 1fr);
    }
}

//SMARTFHONES
@include mediaSm{
    main#home h1 {
        margin-top: 10vh;
    }
    .projects{
        grid-template-columns: 1fr;
    }
}