.menu-btn {
  position: absolute;
  z-index: 3;
  right: 35px;
  top: 35px;
  cursor: pointer;
  @include easeOut;

  .btn-line {
    width: 28px;
    height: 3px;
    margin: 0 0 5px 0;
    background: set-text-color($primaryColor);
    @include easeOut;
  }
  //rotate into x
  &.close {
    transform: rotate(180deg);

    .btn-line {
      //line1 into
      &:nth-child(1) {
        transform: rotate(45deg) translate(5px, 5px);
      }
      //line2
      &:nth-child(2) {
        opacity: 0;
      }
      //line 3
      &:nth-child(3) {
        transform: rotate(-45deg) translate(7px, -6px);
      }
    }
  }
}
//Menu Overlay
.menu {
  position: fixed;
  top: 0;
  width: 100%;

  visibility: hidden;

  &.show {
    visibility: visible;
  }
  &-branding,
  &-nav {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    float: left;
    width: 50%;
    height: 100vh;
    overflow: hidden;
  }
  &-nav {
    margin: 0;
    padding: 0;
    background: rgba(darken($primaryColor, 5), 0.9);

    list-style: none;
    transform: translate3d(0, -100%, 0);
    @include easeOut;
    &.show {
      //Slide in from top
      transform: translate3d(0, 0, 0);
    }
  }

  &-branding {
    background: $primaryColor;
    background: rgba($primaryColor, 0.9);
    transform: translate3d(0, 100%, 0);
    @include easeOut;

    &.show {
      //Slide in from bottom
      transform: translate3d(0, 0, 0);
    }

    .portrait {
      width: 250px;
      height: 250px;
      background: url("../../resources/img/descarga.png");
      border-radius: 50%;
      border: solid 3px $secondaryColor;
    }
  }
  .nav-item {
    transform: translate3d(600px, 0, 0);
    @include easeOut;
    &.show {
      transform: translate3d(0, 0, 0);
    }

    &.current > a {
      color: $secondaryColor;
    }
  }

  .nav-link {
    display: inline-block;
    position: relative;
    font-size: 30px;
    text-transform: uppercase;
    padding: 1rem 0;
    font-weight: 300;
    color: set-text-color($primaryColor);
    text-decoration: none;
    @include easeOut;

    &:hover {
      color: $secondaryColor;
    }
  }
}

@for $x from 1 through 4 {
  .nav-item:nth-child(#{$x}) {
    transition-delay: $x * 0.1s;
  }
}
